<template>
    <div class="product" >
        <div class="img-box" @click="enterDetail(product.pid)">
            <img class="auto-img" :src="product.smallImg">
        </div>
        <div class="pro-info">
            <p class="pro-name">{{product.name}}</p>
            <p class="pro-desc">{{product.enname}}</p>
            <div class="pro-price">
                <div class="price">&yen;{{product.price}}</div>
                <div class="delete-icon">
                    <slot name="delete"></slot>
                </div>
            </div>
        </div>
        <slot name="hot"></slot>
    </div>
</template>

<script>
import '../assets/less/product.less'
    export default {
        name:'Product',
        props:{
            product:{
                type:Object,
                default(){
                    return {}
                }
            }
        },
        methods:{
            //进入详情
            enterDetail(pid){
                this.$router.push({name:'Detail',params:{pid}});
            }
        }
    }
</script>

