<template>
    <div class="home">
        <van-nav-bar>
            <template #left>
                <div class="home-title">
                    <div>{{currentTime}}</div>
                    <div class="username one-text">{{userData.nickName}}</div>
                </div>
            </template>
            <template #right>
                <van-search v-model="searchName" placeholder="输入商品名称" shape="round" @search="search" />
            </template>
        </van-nav-bar>
        <van-swipe @change="changeCurrentIndex" autoplay="3000">
            <van-swipe-item v-for="(item,index) in bannerData" :key="index">
                <img @click="viewProduct(item.pid)" class="auto-img" :src="item.bannerImg">
            </van-swipe-item>
            <template #indicator>
                <div class="custom-indicator">
                    <div v-for="(v,i) in bannerData" class="indicator-item" :class="{'active':currentIndex==i}" :key="i"></div>
                </div>
            </template>
        </van-swipe>
        <div class="product-box">
            <div class="line">
                <span>热卖推荐</span>
            </div>
            <div class="product-items">
                <Product class="product-item" v-for="(item,index) in isHotData" :key="index" :product="item">
                    <template #hot >
                        <div class="hot-box">hot</div>
                    </template>
                </Product>
            </div>
            <div class="product-type-box">
                <div class="type" v-for="(item,index) in typeArr" :key="index">
                    <div class="line">
                        <span>{{item.type}}</span>
                    </div>
                    <div class="product-items">
                        <Product class="product-item" v-for="(value,i) in typeAllArrs[index]" :key="i" :product="value">
                            <template #hot v-if="value.isHot">
                                <div class="hot-box">hot</div>
                            </template>
                        </Product>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import '../assets/less/home.less'
    import Product from '../components/Product'
    import {mapMutations, mapState} from 'vuex'
    export default {
        name:"Home",
        data(){
            return {
                currentIndex:0,//轮播图片当前下标
                bannerData:[],//轮播图data
                isHotData:[],//热卖data
                typeArr:[],//商品类型data
                typeAllArrs:[],//所有商品data
                currentTime:"",
                searchName:""
            }
        },
        methods:{
            changeCurrentIndex(index){
                this.currentIndex = index;
            },
            getBannerData(){
                //加载提示
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });

                this.axios({
                    method:'GET',
                    url:this.baseUrl + '/banner',
                    //get此处修改为params  post为data
                    params:{
                        appkey:this.appkey
                    }
                }).then(res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //请求成功
                    if(res.data.code == 300){
                        this.bannerData = res.data.result;
                    }else {
                        this.$toast({
                            msg:"加载失败",
                            forbidClick:true,
                            duration:1000
                        })
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            getHotData(){
                //加载提示
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });
                //获取热卖商品
                this.axios({
                    method:'GET',
                    url:this.baseUrl + '/typeProducts',
                    params:{
                        appkey:this.appkey,
                        key:'isHot',
                        value:1
                    }
                }).then(res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //请求成功
                    if(res.data.code == 500){
                        this.isHotData = res.data.result;
                    }else {
                        this.$toast({
                            msg:"加载失败",
                            forbidClick:true,
                            duration:1000
                        })
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            getGoodsTypeData(){
                //获取商品类型
                this.axios({
                    method:"GET",
                    url:this.baseUrl + '/type',
                    params:{
                        appkey:this.appkey
                    }
                }).then( res => {
                    if(res.data.code == 400){
                        this.typeArr = res.data.result;
                        for(let key of this.typeArr) {
                            //获取商品类型对应的商品
                            this.axios({
                                method:"GET",
                                url:this.baseUrl + "/typeProducts",
                                params:{
                                    appkey:this.appkey,
                                    key:'type',
                                    value:key.type
                                }
                            }).then( result => {
                                if(result.data.code == 500) {
                                    this.typeAllArrs.push(result.data.result);
                                }
                            })
                        }
                    }
                })
            },
            viewProduct(pid){
                this.$router.push({name:'Detail',params:{pid}})
            },
            //获取昵称
            getName(){
                if(this.isLoadUserdata){
                    return ;
                }
                let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，直接退出
                if(!tokenString){
                    return ;
                }
                //加载提示
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'GET',
                    url:this.baseUrl + '/findAccountInfo',
                    params:{
                        appkey:this.appkey,
                        tokenString
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确，//直接退出
                    if(res.data.code == 700) {
                        return ;
                    }
                    //tokenString正确
                    if(res.data.code == "B001"){
                        this.changeUserData(res.data.result[0]);
                        this.changeIsLoadUserdata(true);
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            //获取当前时间
            getCurrentTime(){
                let date = new Date();
                let hours = date.getHours();
                if(hours >= 0 && hours <= 6){
                    this.currentTime = "凌晨好"
                }
                if(hours > 6 && hours < 12) {
                    this.currentTime = "上午好"
                }
                if(hours >= 12 && hours < 14){
                    this.currentTime = "中午好"
                }
                if(hours >= 14 && hours <= 18) {
                    this.currentTime = "下午好"
                }
                if(hours > 18 && hours < 24 ) {
                    this.currentTime = "晚上好"
                }
            },
            //搜索
            search(){
                let goodsname = this.searchName;
                if(goodsname){
                    this.$router.push({name:"SearchResult",params:{goodsname}});
                }
            },
            ...mapMutations(["changeIsLoadUserdata","changeIsLoadUserdata","changeUserData"])
        },
        created(){
            this.getBannerData();
            this.getHotData();
            this.getGoodsTypeData(); 
            this.getName();   
            this.getCurrentTime();
        },
        components:{
            Product
        },
        computed:{
            ...mapState(['isLoadUserdata',"userData"])
        }
    }
</script>

